<template>
  <p class="joke-text" v-if="words.joke">{{ words.joke }}</p>
  <p class="joke-text" v-else>Click the button to add a joke.</p>
  <button class="btn-change" @click="push()">Change Joke</button>
</template>

<script>
export default {
  data() {
    return {
      words: "Click the button to add a joke",
    };
  },
  methods: {
    async push() {
      await this.$store.dispatch("realWordChange");
      this.words = this.$store.getters.getWord;
    },
  },
};
</script>

