import {
  createStore
} from 'vuex'

const headers = {
  Accept: 'application/json'
};

export default createStore({
  state: {
    thisWord: '',
    words: []
  },
  mutations: {
    changeWord(state, payload) {
      state.words.push(payload)
    },
    changeString(state, payload) {
      state.thisWord = payload
    }
  },
  actions: {
    async realWordChange(state) {
      const change = await fetch(
        'https://icanhazdadjoke.com/', { headers }
      )
      const w = await change.json()
      state.commit('changeString', w)
      state.commit('changeWord', w)
    }
  },
  modules: {},
  getters: {
    getWord(state) {
      return state.thisWord
    },
    getArray(state) {
      return state.words
    }
  }
})